import React from "react";
import { Box, Paper, Typography } from "@mui/material";

function Project({ mode, name, imageSrc, description, index, variant }) {
  console.log(imageSrc);
  return (
    <Paper
      sx={{
        display: "flex",
        flexDirection: "column",
        // justifyContent: "space-between",
        alignItems: "center",
        borderRadius: "20px",
        padding: "20px",
        backgroundColor: "#252F45",
        width: "33%",
      }}
    >
      <Box
        sx={{
          display: "flex",
          gap: "20px",
          marginBottom: "20px",
          transition: "all 0.5s",
          "&:hover": {
            transform: "scale(1.5)",
          },
        }}
      >
        {variant === "pc" ? (
          <img src={imageSrc} style={{ height: "200px" }} />
        ) : (
          imageSrc.map((value, index) => {
            return <img key={index} src={value} style={{ height: "200px" }} />;
          })
        )}
      </Box>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          justifyContent: "flex-start",
          alignItems: "center",
        }}
      >
        <Typography
          sx={{
            fontSize: { xs: "15px", sm: "15px", md: "15px", lg: "20px" },
            color: mode === "dark" ? "#777BA4" : "#36367B",
          }}
          fontFamily={"sans-serif"}
        >
          {name}
        </Typography>
        <Typography
          sx={{
            fontSize: { xs: "15px", sm: "15px", md: "15px", lg: "20px" },
            color: mode === "dark" ? "#A9A3C1" : "#575380",
          }}
          fontFamily={"sans-serif"}
        >
          {description}
        </Typography>
      </Box>
    </Paper>
  );
}

export default Project;

