import "./app.scss";
import ProjectsIntro from "./components/projects/ProjectsIntro";
import About from "./components/about/About";
import Project from "./components/projects/Project";
import { Box } from "@mui/material";
import pixelgramImage from "./assets/pixelgram.png";
import crowdChordLoginImage from "./assets/CrowdChordLogin.png";
import crowdChordArtistsImage from "./assets/CrowdChordArtistsPage.png";
import React, { useRef } from "react";
import Fade from "@mui/material/Fade";
import Navbar from "./components/Navbar/Navbar";
import ScrollTop from "./components/ScrollTop/ScrollTop";
import { useSelector } from "react-redux";
import Grid from "@mui/material/Grid";

const projects = [
  {
    variant: "mobile",
    projectName: "CrowdChord",
    imageSrc: [crowdChordLoginImage, crowdChordArtistsImage],
    description:
      "Developed application to help artists connect with their audience using React Native and Firebase. Implemented secure login using Google Firebase, ensuring data protection and user privacy",
  },
  {
    variant: "pc",
    projectName: "Pixelgram",
    imageSrc: pixelgramImage,
    description:
      "Created a full stack social media web application using Angular, HTML, CSS, and Spring. Developed a robust REST API with Spring to handle queries and authentication for a seamless UX.",
  },
  {
    variant: "pc",
    projectName: "FlavorQuest",
    imageSrc: pixelgramImage,
    description:
      "Developeb a web application to help users decide where to dine using React Native and Firebase,Implemented secure login and registration system using Firebase authentication.Utilized HTTP requests and user location  to query the Yelp API for local restaurant information.",
  },
];

const Projects = ({ projects, mode }) => {
  return (
    <Box
      id="projects"
      sx={{
        scrollMarginTop: "60px",
        display: "flex",
        justifyContent: "space-around",
        alignItems: "space-around",
        gap: "20px",
        padding: "100px",
      }}
    >
      {projects.map((value, index) => {
        return (
          <Project
            mode={mode}
            name={value.projectName}
            imageSrc={value.imageSrc}
            description={value.description}
            index={index}
            variant={value.variant}
          />
        );
      })}
    </Box>
  );
};

function App() {
  let mode = useSelector((state) => state.colorMode.value);

  const [scrollPosition, setScrollPosition] = React.useState(0);
  const handleScroll = () => {
    const position = window.pageYOffset;
    setScrollPosition(position);
  };

  React.useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <Box>
      <Navbar mode={mode} />
      <Grid
        container
        spacing={2}
        sx={{
          backgroundColor: mode === "dark" ? "#0F172A" : "#FFFFFF",
          scrollBehavior: "smooth",
        }}
      >
        <Grid item xs={12}></Grid>
        {scrollPosition >= 10 && <ScrollTop mode={mode} />}
        <Box
          sx={{ width: "100vw", height: "100vh", scrollMarginTop: "999px" }}
          id="about"
        >
          <About mode={mode} />
        </Box>
        <ProjectsIntro mode={mode} />
        <Projects projects={projects} mode={mode} />
        <Box></Box>
      </Grid>
    </Box>
  );
}

export default App;
